import { EnhancedSuggestion } from "@rp/react/tanstack-api/suggestion"

type PanelConfig = {
  discoveryPanelCategories: Array<DiscoveryPanelCategory>
}

type DiscoveryPanelCategory = {
  label: string // Display label for the category
  queryType: string // entity | topic | language | source
  icon: string // icon for the category
  category: string // key for the category in the API
  sortable: boolean // is the category sortable
  groups: Array<DiscoveryPanelGroup | DiscoveryPanelMetadata> // groups for the category
  watchlistFilter: boolean
  watchlistGroups: WatchlistGroups
}

type DiscoveryPanelGroup = {
  label: string
  group: string | null
  enabled: boolean
  default?: boolean
}

type DiscoveryPanelMetadata = {
  enabled: false
  label: string
  metadata: "metadata1" | "metadata2"
}

type WatchlistGroups = {
  title: string
  fields: Array<WatchlistGroupField>
  // Should be excluded from "All" request
  allExcluded?: boolean
}

type WatchlistGroupField = {
  label: string
  key: keyof EnhancedSuggestion | "group1" | "group2" | "group3" | "group4" | "group5"
  onlyDisplay?: boolean
}

const isGroup = (
  group: DiscoveryPanelGroup | DiscoveryPanelMetadata,
): group is DiscoveryPanelGroup => {
  return (group as DiscoveryPanelGroup).group !== undefined
}

const isMetadata = (
  metadata: DiscoveryPanelGroup | DiscoveryPanelMetadata,
): metadata is DiscoveryPanelMetadata => {
  return (metadata as DiscoveryPanelMetadata).metadata !== undefined
}

export type { PanelConfig, DiscoveryPanelGroup, DiscoveryPanelCategory, WatchlistGroups }
export { isGroup, isMetadata }
